@import 'variables';

.nav-fixed {
   width: 100%;
   height: 44px;
   background: #000;
   display: flex;
   color: rgba(204, 204, 204, 0.8);
   font-size: 11px;
   ul {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex: 1 0;
      max-width: 1000px;
      margin: 0 auto;
   }
   li {
      padding: 0 5px;
      line-height: 11px;
      font-weight: 400;
      cursor: pointer;
      transition: color 400ms ease;
      &:hover {
         color: #f5f5f7;
      }
   }
}

.nav-sticky {
   width: 100%;
   position: sticky;
   top: 0;
   z-index: 999;
   padding: 15px 0;
   border-bottom: 1px solid #ccc;
   font-size: 12px;
   font-weight: 400;
   background: rgba(255,255,255,0.7);
   backdrop-filter: saturate(180%) blur(20px);
   .container {
      max-width: 1000px;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin: 0 auto;
      .left p {
         &:first-child {
            color: rgba(0, 0, 0, 0.6);
         }
         &:last-child {
            font-size: 21px;
            line-height: 23px;
            font-weight: 600;
            color: rgba(0, 0, 0, 0.9);
         }
      }
      .right {
         display: flex;
         align-items: center;
         li {
            margin-right: 20px;
            color: rgba(0, 0, 0, 0.9);
            cursor: pointer;
            &:hover {
               color: $apple-blue;
            }
            &:first-child {
               color: rgba(0, 0, 0, 0.5);
            }
         }
      }
      button {
         background: #0071e3;
         color: rgba(255, 255, 255, 0.9);
         border: none;
         border-radius: 999px;
         padding: 4px 11px;
         cursor: pointer;
         &:hover {
            background: #1d7ce5;
         }
      }
   }
}
